<template>

  <el-row :gutter="10">
    <div v-for="(data, index) in objs" :key="index">
      <el-col :span="item_span">
        <el-card :body-style="{ padding: '5px' }">


          <div class="aaa">

            <div v-viewer class="image_main1">
              <div v-if="data.urls.length<=1" class="image_main">
                <img v-bind:src="data.url0" class="image">
              </div>

              <swiper v-else :options="swiperOption" class="image_swiper">
                <swiper-slide v-for="(item,index) in data.urls" :key="index" class="image_main">
                  <img v-bind:src="item" class="image">

                </swiper-slide>

                <div class="swiper-pagination" slot="pagination"></div>

              </swiper>


              <!--<Swiper class="image_main0"  v-if="data.urls.length > 0" :autoPlay='false' :showIndicator='data.urls.length > 1'>-->
              <!--<Slide ref="swiper"   v-for="(item,index) in data.urls" :key="index" class="image_main">-->
              <!--<img v-bind:src="item" class="image">-->
              <!--</Slide>-->

              <!--</Swiper>-->
            </div>

            <!--<div class="demo-image__preview">-->
            <!--<el-image-->
            <!--class="image"-->
            <!--style="width: 100px; height: 100px"-->
            <!---->
            <!--:src="data.url0"-->
            <!--:preview-src-list="data.urls">-->
            <!--</el-image>-->
            <!--</div>-->


            <div class="content">
              <span>{{ data.description}}</span>
            </div>
          </div>
        </el-card>
      </el-col>
    </div>
  </el-row>
</template>

<script>

  import {Swiper, Slide} from 'vue-swiper-component';


  export default {
    name: "DataCard",
    props: {
      showNumber: Number,
      objs: Array,
      ot: Number
    },
    data() {
      return {

        item_span: 24 / this.showNumber,
        swiperOption: {
          effect: 'coverflow',
          grabCursor: true,
          centeredSlides: true,
          slidesPerView: 'auto',
          coverflowEffect: {
            rotate: 50,
            stretch: 0,
            depth: 100,
            modifier: 1,
            slideShadows : true
          },
          pagination: {
            el: '.swiper-pagination'
          }
        }
      }
    },
    mounted() {
      // console.log(this.objs)
      console.log('this is current swiper instance object', this.swiper)
      // this.$refs.mySwiper.swiper.slideTo(3, 1000, false)
      // this.swiper.slideTo(3, 1000, false)
    },

    methods: {},
    components: {
      // Swiper, Slide,

    }

  }
</script>

<style scoped>

  .el-card{
    margin-top: 10px;
  }
  .aaa {
    height: 300px;
    display: flex;
    flex-direction: column;

  }


  .content {
    padding: 14px;
    display: flex;
    height: 10%;
    flex-direction: column;
    justify-content: center;

  }

  .image_main1 {
    display: flex;
    height: 90%;

    flex-direction: row;
    justify-content: center;
  }

  .image_swiper {

    display: flex;
    height: 100%;
    flex-direction: row;
    justify-content: center;
  }

  .image_main {

    display: flex;
    height: 100%;
    flex-direction: row;
    align-items: center;
    justify-content: center;
  }

  .image {
    width: 40%;
  }




</style>



